@import '../../vars';

.logo {
  width: 30px;
}

.header {
  grid-area: h;
  background-color: rgb(78, 233, 181);
  border-radius: 0px 0px 15px 15px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.loginLink {
  @extend %button-style-2 ;
  
}

.loginLink,
.loginName {
  font-weight: 500;
}

.loginControls {
  display: flex;
  align-items: center;
}

.loginName {
  margin-right: 20px;
}

.logoutBtn {
  @extend %button-style-2 ;
}